.login {
  width: 100%;
  height: 100%;
  background: ~'url("../img/bg.jpg") no-repeat 0 0 / 100% 100%';
  .login-header {
    display: flex;
    align-items: center;
    height: 80px;
    background-color: rgba(21, 20, 13, 0.5);
    img {
      width: 40px;
      height: 40px;
      margin: 0 15px 0 50px;
    }
    h1 {
      font-size: 30px;
      color: #fff;
      margin: 0;
    }
  }
  .login-content {
    width: 400px;
    height: 300px;
    background-color: #fff;
    margin: 50px auto;
    padding: 20px 40px;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    .error-msg {
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      height: 30px;
      width: 100%;
      background-color: #f60c1a;
      color: #fff;
      font-size: 16px;
      transform: translateY(-30px);
      transition: all .3s;
      &.show {
        visibility: visible;
        transform: translateY(0);
      }
    }
    h2 {
      text-align: center;
      font-size: 30px;
      font-weight: 700;
      margin-bottom: 20px;
    }
    .login-form {
      .login-icon {
        color: rgba(0, 0, 0, 0.25);
      }
    }
  }
}

@primary-color: #1DA57A;